/* Dashboard Sidebar Floating Panel : Custom Styles */
[data-floating-panel-trigger] [data-floating-panel-trigger-btn][data-is-active="true"] {
  background: rgba(251, 91, 162, 0.2); }

[data-floating-panel-trigger] [data-floating-panel-trigger-btn][data-is-active="true"] [data-sidebar-menu-item-text] {
  color: var(--color-rose); }

[data-floating-panel-trigger] [data-floating-panel-trigger-btn][data-is-active="true"] img {
  filter: brightness(0) saturate(100%) invert(29%) sepia(98%) saturate(5809%) hue-rotate(325deg) brightness(92%) contrast(121%); }

[data-floating-panel-wrapper] [data-floating-panel] {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-is-active="false"] {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(0) translateY(4rem);
  transform-origin: left bottom; }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-is-active="true"] {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: scale(1) translateY(4rem);
  transform-origin: left bottom; }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-position-lower="8rem"][data-is-active="false"] {
  transform: scale(0) translateY(8rem); }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-position-lower="8rem"][data-is-active="true"] {
  transform: scale(1) translateY(8rem); }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-position-lower="10rem"][data-is-active="false"] {
  transform: scale(0) translateY(10rem); }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-position-lower="10rem"][data-is-active="true"] {
  transform: scale(1) translateY(10rem); }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel] [data-sidebar-menu-item] {
  width: 4.625rem;
  height: 3.5rem; }

[data-floating-panel-wrapper][data-is-floating="true"] [data-floating-panel][data-single-item="true"] {
  width: 6.125rem;
  grid-template-columns: repeat(1, 1fr);
  right: -7.125rem; }

